import React, { useState } from 'react'
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab'
import Accepted from './components/accept_challenges'
import Pending from './components/pending_challenges'
import Completed from './components/completed_challenges'



const Section_challenges = () => {

    const useStyles = makeStyles({
        root: {
          flexGrow: 1,
        },
      });

      const classes = useStyles();
      const [value, setValue] = useState(0);
    
      const handleChange = (event, newValue) => {
        setSelectedTab(newValue);
      };

      const [SelectedTab, setSelectedTab] = React.useState(1);

      


    return (


        <div className="challenges">

                <Paper className={classes.root}>
                    <Tabs
                        value={SelectedTab}
                        onChange={handleChange}
                        indicatorColor="primary"
                        textColor="primary"
                        centered
                    >
                        <Tab label="Pending" />
                        <Tab label="Approved" />
                        <Tab label="Completed" />

                    </Tabs>
                    </Paper>



        {SelectedTab === 0 && <Pending/>}
        {SelectedTab === 1 && <Accepted/> }
        {SelectedTab === 2 && <Completed/>}


            
        </div>
    )
}

export default Section_challenges
