import React, { useState, useEffect } from 'react'
import axios from 'axios';
import {useDataLayerValue} from '../../ContexApi/DataLayer'
import Avatar from '@material-ui/core/Avatar';
import CheckCircleOutlineOutlinedIcon from '@material-ui/icons/CheckCircleOutlineOutlined';
import CancelOutlinedIcon from '@material-ui/icons/CancelOutlined';
import IconButton from '@material-ui/core/IconButton';
import TimerIcon from '@material-ui/icons/Timer';
import InfoOutlinedIcon from '@material-ui/icons/InfoOutlined';


const Section_cards = () => {

    const [cards, setCards] = useState([
    ]);

    const [Error, setError] = useState([
    ]);

    const [{user}, dispatch] = useDataLayerValue();

    
    useEffect(() => {

        async function userCards() {

        await axios

          .get(`http://localhost:3000/api/user/cards/${user.id}`)
          .then(response =>  setCards(response.data.cards)
          
          ).catch(error => {
            console.log(error.response)
            setError(error.response)
          })

        }

        userCards()
      }, []);


    return (
        <div>

            <div className="profile_cards">
                {cards.map((card) => (
                    <div className="profile_card" key={card.id}> 
                        <span> {card.content} </span>
                    </div>     
                ))}

            </div>

        </div>
    )
}

export default Section_cards
