import React, { useState, useEffect } from 'react'
// import PrettoSlider from '@material-ui/core/Slider';
import WifiIcon from '@material-ui/icons/Wifi';
import { withStyles, makeStyles } from '@material-ui/core/styles';
import Slider from '@material-ui/core/Slider';
import SignalWifi0BarIcon from '@material-ui/icons/SignalWifi0Bar';
import SignalWifi1BarIcon from '@material-ui/icons/SignalWifi1Bar';
import SignalWifi2BarIcon from '@material-ui/icons/SignalWifi2Bar';
import SignalWifi4BarIcon from '@material-ui/icons/SignalWifi4Bar';
import QuranIcon from '../../static/img/quran-icon.svg'
import PrayersIcon from '../../static/img/my-prayers-icon.svg'
import FastsIcon from '../../static/img/my-fasts-icon.svg'
import OtherIcon from '../../static/img/other-icon.svg'
import IconButton from '@material-ui/core/IconButton';




const Section_profile = () => {

    const useStyles = makeStyles((theme) => ({
        root: {
          width: 300 + theme.spacing(3) * 2,
        },
        margin: {
          height: theme.spacing(3),
        },
      }));
    
    const [value, setValue] = useState(50);

    const handleChange = (event, newValue) => {
      setValue(newValue);
    };

    const PrettoSlider = withStyles({
        root: {
          color: '#b69a5c',
          height: 8,
        },
        thumb: {
          height: 24,
          width: 24,
          backgroundColor: '#b69a5c',
          border: '2px solid currentColor',
          marginTop: -8,
          marginLeft: -12,
          '&:focus, &:hover, &$active': {
            boxShadow: 'inherit',
          },
        },
        active: {},
        valueLabel: {
          left: 'calc(-50% + 4px)',
        },
        track: {
          height: 8,
          borderRadius: 4,
        },
        rail: {
          height: 8,
          borderRadius: 4,
        },
      })(Slider);

      const classes = useStyles();

      console.log(value)



    return (
        <div>

            <div className="profile_emaan_status">

            <h2> Emaan Status </h2>


            </div>

            <div className="profile_emaan_slider">

            <SignalWifi0BarIcon/>
                  <Slider onChange={handleChange} value={value} defaultValue={50} step={25} marks min={0} max={100} valueLabelDisplay="auto"/>

                  {value > 0 && value < 50 ? (
                    <SignalWifi1BarIcon/>

                  ):            
                  (
                    <SignalWifi0BarIcon/>
                  ) }

            </div>

            <div className="profile_icons"> 

            <div className="profile_icon_tab">
              <div className="profile_icon_tab_name">
                <img src={QuranIcon}></img>
                <span> Quran </span>
              </div>
            </div>

            <div className="profile_icon_tab">
              <div className="profile_icon_tab_name">
                  <img src={PrayersIcon}></img>
                  <span> Prayer </span>
              </div>
            </div>

            <div className="profile_icon_tab">
              <div className="profile_icon_tab_name">
                  <img src={FastsIcon}></img>
                  <span> Fasts </span>
              </div>
            </div>

            <div className="profile_icon_tab">
              <div className="profile_icon_tab_name">
                  <img src={OtherIcon}></img>
                  <span> Other </span>
              </div>
            </div>


            
            </div>


            
        </div>
    )
}

export default Section_profile
