import React from 'react'
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import LikesIcon from '@material-ui/icons/FavoriteBorder';




function Comment({name, comment, displayPic, timestamp, likes, match}) {

    return (

        <div className="comment">
        <Avatar className="comment_card_avatar"  alt={name} src={displayPic} />
        <div className="comment_details">
        <h5>@{name}</h5>
        <div className="comment_comment">
            <p>{comment}</p>
        </div>    
            <div className="comment_likes"> 
            <p>{likes} Likes</p>
            <p>{timestamp}</p>

            </div>

        </div>
        <div className="comment_timestamp">
        <IconButton> <LikesIcon  className="likes_icon"/> </IconButton>
        </div>
     
        </div>
        
    )
}

export default Comment
