import React, { useState, setState } from 'react'
import Logo from 'static/img/Logo.svg'
import FacebookIcon from 'static/img/Facebook-Icon.svg'
import InstagramIcon from 'static/img/Instagram-Icon.svg'
import TwitterIcon from 'static/img/Twitter-Icon.svg'
import CustomButton from 'Components/Button'
import { Grid } from '@material-ui/core'
import { Link } from 'react-router-dom'
import './Home.css'

const Home = props => {
  return (
    <div className={'homepage'}>
      <img
        style={{ marginTop: '170px' }}
        className={'center'}
        height={'83'}
        width={'83'}
        src={Logo}></img>
      <h1>Muslamica</h1>

      <Grid
        lg={5}
        xs={11}
        direction='column'
        justify='space-between'
        alignItems='center'
        className={'center'}>
        <CustomButton cssClass={'home-button center'} text={'GET STARTED'} />
        <p className={'text-center have-account'}>
          Already have an account? <Link>Login</Link>
        </p>
        <Grid
          container
          alignItems='center'
          className={'mdl-center'}
          lg={4}
          xs={6}
          style={{ marginTop: '100px' }}>
          <Grid item style={{ marginRight: '16px', marginLeft: 'auto' }}>
            <Link>
              <img src={FacebookIcon} />
            </Link>
          </Grid>
          <Grid item style={{ marginRight: '16px', marginLeft: 'auto' }}>
            <Link>
              <img src={InstagramIcon} />
            </Link>
          </Grid>
          <Grid item style={{ marginRight: '16px', marginLeft: 'auto' }}>
            <Link>
              <img src={TwitterIcon} />
            </Link>
          </Grid>
        </Grid>
      </Grid>
    </div>
  )
}

export default Home
