import React, { useState, useEffect } from 'react'
import Header from '../../Components/Header'
import Cards from '../../Components/Cards'
import Footer from '../../Components/Footer'
import {withRouter, useHistory} from 'react-router-dom'
import axios from 'axios';
import Avatar from '@material-ui/core/Avatar';
import ProfileTrophy from '../../static/img/profile-level.png'
import IconButton from '@material-ui/core/IconButton';
import { makeStyles } from '@material-ui/core/styles';
import SectionCards from './section_cards'
import SectoinProfile from './section_profile'
import SectionChallenges from './section_challenges'
import {Link} from "react-router-dom"



import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import LibraryBooksOutlinedIcon from '@material-ui/icons/LibraryBooksOutlined';
import FavoriteIcon from '@material-ui/icons/Favorite';
import PersonOutlineOutlinedIcon from '@material-ui/icons/PersonOutlineOutlined';import LikesIcon from '@material-ui/icons/FavoriteBorder';
import VpnKeyOutlinedIcon from '@material-ui/icons/VpnKeyOutlined';
import GroupOutlinedIcon from '@material-ui/icons/GroupOutlined';
import DehazeIcon from '@material-ui/icons/Dehaze';
import AppBar from '@material-ui/core/AppBar';



import {useDataLayerValue} from '../../ContexApi/DataLayer'


import "./Profile.css"

function Index({match}) {

    let history = useHistory();
  
    const [Newuser, setNewuser] = useState([
    ]);

    const [{user}, dispatch] = useDataLayerValue();

    // console.log("profile user", user)

    
    
    var token = localStorage.getItem('authToken')
  
  useEffect( () => {
  
    async function userCheck() {
  
        if (token) {
  
            await axios
               .get('http://localhost:3000/api/user/logged', {
                 headers: {
                   authToken: token,
                   Accept: 'application/json',
                   'Content-Type': 'application/json'
                 }
               })
               .then(response => {
                 if (response.data === 'Invalid Token') {
                   window.localStorage.removeItem('AuthToken')
                   history.push('/login')
                 }
         
                 if (response.data.User) {
                   console.log('This is the user', response.data.User)
                     setNewuser(response.data.User)
                     dispatch({
                       type: "SET_USER",
                       user: response.data.User
                     })

         
                 }
         
         
               })
               .catch(error => {
                 console.log(error)
               })
           } else {
             console.log('no token found')
             history.push('/login')
           }
    }
    
    userCheck()
    
    
    }, [token]);   

    
    
    const useStyles = makeStyles({
        root: {
          flexGrow: 1,
          maxWidth: 500,
        },
      });


    const classes = useStyles();

  
    const handleChange = (event, newValue) => {
      setSelectedTab(newValue);
    };
  
    const tabNameToInder = {
      0: "profile",
      1: "cards",
      2: "challenges",
      3: "friends",
    };

    const indexToTabName = {
      profile: 0,
      cards: 1,
      challenges: 2,
      friends: 3

    };
    
    const [SelectedTab, setSelectedTab] = React.useState(0);

    
    return (

      <div className="profile_container">

          <div className="profile_header">

          <IconButton> <DehazeIcon className="header_icon"/> </IconButton>

          </div>

        <div className="profile">

    <div className="profile_avatarname">

        <div className="profile_inner">

            <Avatar
                className="profile_card_avatar"
                alt={Newuser.first_name}
                src={Newuser.display_pic}
            />
        <span> {Newuser.first_name}</span>

        </div>

        <div className="profile_trophybio">

            <div className="profile_trophy">
            

        <img src={ProfileTrophy} />{" "}

        </div>

        <span> @{Newuser.username}
        </span>
        <p> {Newuser.bio}</p>


        </div>


    </div>



        </div>



        <div className="profile_tabs"> 


      <AppBar position="static">
        <Tabs value={SelectedTab} onChange={handleChange} variant="fullWidth"> 

          <Tab  component={Link} to={`/profile/${SelectedTab}`} icon={<PersonOutlineOutlinedIcon />} label="Profile" />
          <Tab component={Link} to={`/profile/${SelectedTab}`}  icon={<LibraryBooksOutlinedIcon />}  label="Cards" />
          <Tab component={Link} to={`/profile/${SelectedTab}`}  icon={<VpnKeyOutlinedIcon />} label="Challenges" />
          <Tab component={Link} to={`/profile/${SelectedTab}`}  icon={<GroupOutlinedIcon />} label="Friends" />

      </Tabs>
    </AppBar>

      {SelectedTab === 0 && <SectoinProfile/>}
      {SelectedTab === 1 && <SectionCards/>}
      {SelectedTab === 2 && <SectionChallenges/>}



        </div> 


      </div>
    );
}

export default Index
