import React, { useState, useEffect } from 'react'
import axios from 'axios';
import {useDataLayerValue} from '../../../ContexApi/DataLayer'
import Avatar from '@material-ui/core/Avatar';
import CheckCircleOutlineOutlinedIcon from '@material-ui/icons/CheckCircleOutlineOutlined';
import CancelOutlinedIcon from '@material-ui/icons/CancelOutlined';
import IconButton from '@material-ui/core/IconButton';
import TimerIcon from '@material-ui/icons/Timer';
import InfoOutlinedIcon from '@material-ui/icons/InfoOutlined';


const Accept_challenges = () => {

    
    const [challenges, setChallenges] = useState([
    ]);

    const [Error, setError] = useState([
    ]);

    const [{user}, dispatch] = useDataLayerValue();

    
    useEffect(() => {

        async function approvedChallenge() {

        await axios

          .get(`http://localhost:3000/api/user/challenges/${user.id}`)
          .then(response =>  setChallenges(response.data.challenges)
          
          ).catch(error => {
            console.log(error.response)
            setError(error.response)
          })

        }

        approvedChallenge()
      }, []);


    return (
        <div>

                {challenges.map((challenges) => (

                    <div className="pending_challenge" key={challenges.id} >
                        <div className="challenge">
                        <Avatar className="challenge_card_avatar"  alt="imran" src={challenges.User.display_pic} />
                        <div className="challenge_details">
                            <h5>@{challenges.User.username}</h5>
                        <div className="challenge_comment">
                            <p> you have an upcoming <strong> {challenges.Challenge.type} </strong> challenge with <strong>{challenges.User.first_name}</strong></p>
                        </div>    
                            <div className="challenge_time"> 
                            <p> 2 days to ago</p>
                            </div>

                        </div>
                        <div className="challenge_tickcross">
                        {/* <IconButton> <TimerIcon  className="challenge_icon"/> </IconButton> */}
                        <IconButton> <InfoOutlinedIcon  className="challenge_icon"/> </IconButton>

                        </div>
                    
                        </div>
                    </div>

                ))}

            
        </div>
    )
}

export default Accept_challenges
