import React, { useState, useEffect } from 'react'
import axios from 'axios';
import "./Comments.css"
import Comment from './comment'
import Header from '../Header'
import {useDataLayerValue} from '../../ContexApi/DataLayer'



function Comments({match}) {

    const [{user}, dispatch] = useDataLayerValue();


    const [input, setInput] = useState('')

    const handleSubmit = e => {
        const userId = user.id
        axios.post(`http://localhost:3000/api/user/comment/${match.params.id}`, 
          
        {    
          user_id: userId,
          content: input
          }).then(response => {
              console.log("done")
          })

          // event.preventDefault()


    }


    const  [comments, setComments] = useState([
    ]);
  
    useEffect(() => {
      axios
        .get(`http://localhost:3000/api/user/cards/comment/${match.params.id}`)
        .then(response => setComments(response.data.comments)
        
        );

    }, []);
  
    return (

        <div>
         <Header backButton="/cards"/>
        <div className="comment_container"> 
        {comments.map((comment) => (            
            <Comment
                key={comment.id}
                name= {comment.User.username}
                comment={comment.content}
                timestamp="1hr"
                displayPic={comment.User.display_pic}
                likes= "5"
            
            />


            
            
        ))}
        </div>

        <form  className="comment_input">
            <input
            value ={input}
            onChange={e => setInput(e.target.value)}
            className="comment_input_field" 
            placeholder="add a comment"
            type ="text"
            />
            <button onClick={handleSubmit} type ="submit" className="comment_input_button">SEND</button>
        </form>
      </div>


    )
}

export default Comments
