import React, { useEffect, useState } from 'react'
import CustomButton from 'Components/Button'
import { Link } from 'react-router-dom'
import './Login.css'
import { FormControl, Input, Grid, InputLabel } from '@material-ui/core'
import axios from 'axios';
import {withRouter, useHistory} from 'react-router-dom'
import {useDataLayerValue} from '../../ContexApi/DataLayer'




const Login = props => {
  const [{user}, dispatch] = useDataLayerValue();

  let history = useHistory();

  const [userName, setUserName] = useState()
  const [userPassword, setUserPassword] = useState()
  const [error, setError] = useState()

  const handleChange = event => {
    if (event.target.name === 'password') {
      setUserPassword(event.target.value)
    } else if (event.target.name === 'username') {
      setUserName(event.target.value)
    }
  }

  const handleSubmit = event => {


    if (!userName || !userPassword) {
      setError('Set All Fields!')
      return
    }

    axios.post(`http://localhost:3000/api/user/login`,         
    {    
      email: userName,
      password: userPassword
      }).then(response => {
          console.log(response.data.token)
          window.localStorage.setItem('authToken', JSON.stringify(response.data.token))     
           history.push("/cards");

      }).catch(error=>{
        // console.log(error.response.data.error)
        console.log(error)
        
      })


  }


  return (
    <div>
      <h1 style={{ marginTop: '170px' }}>Log in</h1>

      <div className={'login-page'}>
        <Grid
          lg={5}
          xs={11}
          direction='column'
          justify='space-between'
          alignItems='center'
          className={'center'}>
          <p id={'error-message'}>{error}</p>

          <form
            action='/login'
            mathod='post'
            className='login-form'
            onSubmit={handleSubmit}>
            <div style={{ marginTop: '30px' }}>
              <FormControl>
                <InputLabel htmlFor='username'>Username</InputLabel>
                <Input
                  id='username'
                  type='text'
                  placeholder='username'
                  name='username'
                  onChange={handleChange}></Input>
              </FormControl>
            </div>

            <div style={{ marginTop: '30px' }}>
              <FormControl>
                <InputLabel htmlFor='password'>Password</InputLabel>
                <Input
                  id='password'
                  type='password'
                  placeholder='password'
                  name='password'
                  onChange={handleChange}></Input>
              </FormControl>
            </div>

            <CustomButton
              onClick={handleSubmit}
              cssClass={'login-button center'}
              text={'Login'}
            />
          </form>
        </Grid>

        <p className={'text-center'} style={{ marginTop: '40px' }}>
          Forgot your details? <Link>Get help</Link>
        </p>
      </div>
    </div>
  )
}

export default withRouter(Login)
