import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import HomeIcon from '@material-ui/icons/Home';
import SearchIcon from '@material-ui/icons/Search';
import ControlPointIcon from '@material-ui/icons/ControlPoint';
import RssFeedIcon from '@material-ui/icons/RssFeed';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import IconButton from '@material-ui/core/IconButton';
import {Link} from "react-router-dom"
import {withRouter, useHistory} from 'react-router-dom'
import axios from 'axios';
import Avatar from '@material-ui/core/Avatar';
import NotificationsNoneIcon from '@material-ui/icons/NotificationsNone';






import "./Footer.css"



const Footer = () => {

  let history = useHistory();

  const [Newuser, setNewuser] = useState([
  ]);
  
  var token = localStorage.getItem('authToken')

useEffect( () => {

  async function userCheck() {

    if (token) {
     await axios
        .get('http://localhost:3000/api/user/logged', {
          headers: {
            authToken: token,
            Accept: 'application/json',
            'Content-Type': 'application/json'
          }
        })
        .then(response => {
          if (response.data.User) {
              setNewuser(response.data.User)  
          }
        })
        .catch(error => {
          console.log(error)
        })
    } 
  }
  
  userCheck()
  
  
  }, []);

  return (

  <div className ="footer">
    
    <Link to ={'/cards'} ><IconButton> <HomeIcon className="footer_icon"/> </IconButton></Link>
    <IconButton> <SearchIcon className="footer_icon"/> </IconButton>
    <IconButton> <ControlPointIcon className="footer_icon"/></IconButton>
    <IconButton> <NotificationsNoneIcon className="footer_icon"/></IconButton>
    <Link to ={`/profile`} ><IconButton> <Avatar className="footer_avatar"  alt="user" src={Newuser.display_pic} /></IconButton></Link>

    {/* <div className = "footer_trophy"> <img src="/static/media/profile-level.81a354a2.png"></img></div> */}

  </div>

  )}

export default Footer
