import React from 'react'
import { Button } from '@material-ui/core'

import './Button.css'

const CustomButton = props => {
  return (
    <Button
      disabled={props.disabled}
      variant='contained'
      className={`custom-button ${props.cssClass}`}
      size={props.size}
      href={props.link}
      type={props.form && 'submit'}
      onClick={props.onClick}
      style={props.style}>
      {props.text}
    </Button>
  )
}

export default CustomButton
