import React, { useState, useEffect } from 'react'
import axios from 'axios';
import {useDataLayerValue} from '../../../ContexApi/DataLayer'
import Avatar from '@material-ui/core/Avatar';
import CheckCircleOutlineOutlinedIcon from '@material-ui/icons/CheckCircleOutlineOutlined';
import CancelOutlinedIcon from '@material-ui/icons/CancelOutlined';
import IconButton from '@material-ui/core/IconButton';





const Pending_challenges = () => {


    const [challenges, setChallenges] = useState([
    ]);

    const [Error, setError] = useState([
    ]);

    const [{user}, dispatch] = useDataLayerValue();



    
    useEffect(() => {

        async function pendingChallenge() {

        await axios

          .get(`http://localhost:3000/api/user/challenges-pending/${user.id}`)
          .then(response =>  setChallenges(response.data)
          
          ).catch(error => {

            console.log(error.response)

          })

        }
        pendingChallenge()

      }, []);


    const  handleAcceptChallenge = challengeId => () => {

        const userId = user.id

    async function acceptChallenge() {

         axios.post(`http://localhost:3000/api/user/challenge-accept/${userId}/${challengeId}`, 
          
        {    
          user_id: userId,

          }).then(response => {
              console.log("accepted")
          }).catch(error => {
            console.log(error.response)
          })
    }

    acceptChallenge()

    }

    const  handleDeclineChallenge = challengeId => () => {

        const userId = user.id

    async function declineChallenge() {

         axios.post(`http://localhost:3000/api/user/challenge-decline/${userId}/${challengeId}`, 
          
        {    
          user_id: userId,

          }).then(response => {
              console.log("declined")
          }).catch(error => {
            console.log(error.response)
          })
    }

    declineChallenge()
    
    }


    




      

    return (

        
        <div className="pending_challenges">

           
                {challenges.map((challenge) => (
                    <div className="pending_challenge" key= {challenge.id}>
                        <div className="challenge">
                        <Avatar className="challenge_card_avatar"  alt={challenge.User.first_name} src={challenge.User.display_pic} />
                        <div className="challenge_details">
                        <h5>@{challenge.User.username}</h5>
                        <div className="challenge_comment">
                            <p> <strong>{challenge.User.first_name}</strong> has challenged you to a <strong>{challenge.Challenge.type}</strong></p>
                        </div>    
                            <div className="challenge_time"> 
                            <p> 1hr ago</p>

                            </div>

                        </div>
                        <div className="challenge_tickcross">
                        <IconButton  onClick={handleAcceptChallenge(challenge.Challenge.id)}> <CheckCircleOutlineOutlinedIcon  className="challenge_icon"/> </IconButton>
                        <IconButton onClick={handleDeclineChallenge(challenge.Challenge.id)}> <CancelOutlinedIcon  className="challenge_icon"/> </IconButton>
                        </div>
                    
                        </div>
                    </div>

                ))}

            
        </div>
    )
}

export default Pending_challenges
